import React, { Component,createRef } from 'react'

import { Card, Input, Button,DatePicker,Spin,message,Row,Col} from 'antd';
import { Form,Icon} from '@ant-design/compatible';
import '@ant-design/compatible/assets/index.css';

import E from 'wangeditor'
import moment from 'moment'
import  './capsule.less'

import {getArticleById,saveArticle,uploadCapsule} from '../../../../services'

const formItemLayout = {
    labelCol:{
        span:3
    },
    wrapperCol:{
        span:18
    }
}

// 如何 获得  this.props.form
// 必须使用   Form.create()  方法  返回的方法，将整体组件 用参数的方式传入  返回的方法 中
// 返回的方法  会返回一个 高阶组件（只是 对一个组件 进行了加工）

// @Form.create()  装饰器 模式的写法，
//等价于   export default const Edit = Form.create()(Edit);

//  上述一番操作之后， 就可以使用 this.props.form  获得关于  antd Form 组件的 一些信息，以及 方法


@Form.create()
class UploadCapsule extends Component {
    constructor(){
        super()
        // 获得  ref 标记的 虚拟DOM
        this.editorRef=createRef()
        this.state={
            isLoading:false,
            dataSource:{}
        }
    }
    handleSubmit = (e)=>{
        e.preventDefault()
        this.setState({
            isLoading:true
        })
        this.props.form.validateFields((err, values) => {
            if (!err) {
              console.log('Received values of form: ', values);
              const newTime = values.capsule_time.valueOf()
              const data = Object.assign({},values,{
                  capsule_time:String(newTime),
              })
              uploadCapsule(data)
              .then(resp =>{
                if(resp.data===1){
                    message.success('上传成功')
                    this.props.history.push('/admin/info/capsule')
                }
              })
              .catch(()=>{
                  message.error('上传失败')
              })
              .finally(()=>{
                this.setState({
                    isLoading:false
                })
            })
            }
          });
    }
    initEditor = () => {
        this.editor = new E(this.editorRef.current)
        //因为  this.editorRef  是虚拟DOM，需要使用  current 
        this.editor.customConfig.onchange = (html) => {  
            // 这里是  wangeditor 带的方法，每当 编辑器内容有所改变便会触发。  参数 html ，是 html式 的文本内容
            this.props.form.setFieldsValue({
                //   setFieldsValue,  对   {getFieldDecorator('content',....   这里定义的  'content' 进行赋值
                capsule_content: html
            })
        }
        this.editor.create()
    }
    fillFormData = (source)=>{
        if(source){
            const {capsule_id, ...data} = source
            data.capsule_time = moment(data.capsule_time)
            // console.log(data)
            this.props.form.setFieldsValue(data)
            this.editor.txt.html(data.capsule_content)
            this.setState({
                isLoading:false
            })
        }
    }
    componentDidMount(){
        this.initEditor()
        this.setState({
            isLoading:true,
        })
        this.fillFormData(this.props.dataSource)
        
        // getArticleById(this.props.match.params.id)
        // .then(resp =>{
        //     const {id, ...data} = resp
        //     data.capsule_time = moment(data.capsule_time)
        //     this.props.form.setFieldsValue(data)
        //     this.editor.txt.html(data.content)
        // })
        // .finally(()=>{
        //     this.setState({
        //         isLoading:false
        //     })
        // })
        
    }
    onClickBack=()=>{
        this.props.history.push('/admin/info/capsule')
    }
    render() {
        const{
            getFieldDecorator
        }=this.props.form
        // this.props.form  是 antd Form组件 自带的  一些组件内的  值 与 方法
        // 使用  Form 组件必须使用
        return (
            
                <Card  
                    title={"时讯"}
                    bordered={false} 
                    extra={<Button onClick={this.onClickBack}> 取消 </Button>} 
                >
                    <Form 
                        onSubmit={this.handleSubmit} 
                        className="login-form"
                        {...formItemLayout}
                    >
                        <Form.Item
                            label="标题"
                        >
                        {getFieldDecorator('capsule_title', {
                            rules: [{ required: true, message: '标题是必填项' }],
                        })(
                            <Input
                                placeholder="标题"
                            />,
                        )}
                        
                        </Form.Item>
                        <Form.Item
                            label="创建时间"
                        >
                        {getFieldDecorator('capsule_time', {
                            rules: [
                                { required: true, message: '时间是必填项' },
                            ],
                        })(
                            <DatePicker showTime placeholder="Select Time"  />
                        )}
                        </Form.Item>
                        <Form.Item
                            label="主体内容"
                        >
                        {getFieldDecorator('capsule_content', {
                            rules: [
                                { required: true, message: '内容是必填项' },
                            ],
                        })(
                            //  如何获得一个虚拟 DOM。如下
                            //  使用  ref 属性标记一个虚拟 DOM，如： <div ref={this.editorRef}> </div>标记为 this.editorRef
                        <div className='my-editor' ref={this.editorRef} >
                            
                        </div>
                        )}

                        </Form.Item>
                        <Row>
                            <Col span={2} offset={2}>
                                <Button   type="primary" htmlType="submit" className="login-form-button">
                                    确定
                                </Button>
                            </Col>
                        </Row>
                    </Form>
                </Card>
        )
    }
}
export default UploadCapsule
